//  Ingreso con un usuario genérico de hoyostools para poder hacer las consultas
export const loginEventVR = () => {

    const url = "https://p297hirw91.execute-api.us-east-1.amazonaws.com/v1/"
    
    const bodyLogin = {
        "request_data": {
        "type_action": "login",
            "data": {
                "correo_usuario": "gerente3@hoyostools.com",
                "clave_usuario": "123456"
            }
        },
        "headers": {
            "Content-Type": "application/json",
            "Accept": "application/json"
        }
    }

    // Axios
    axios.post(url + 'login', bodyLogin, {
        headers: {
            "Content-Type": "application/json",
            "Accept": "application/json",
        },
    })
    .then(async(response) => {
        console.log('respuesta :', response.data.status)
       
        if(response.status == '200' && response.data.status != 'failed'){
            
            let EventVRToken = response.data.result.token;
            let idEmpresa = response.data.result.user_data.id_empresa_usuario;
            window.localStorage.setItem('adminToken', EventVRToken);
            //console.log('idempresa: ', response.data.result)
            getLocales(EventVRToken, idEmpresa)
            getPubli(EventVRToken)

            
            

        }
    })
    .catch(error => {
        console.log('error : ', error)
    });
}

