import React from 'react';



import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

import CustomCard from '../../Cards/CustomCard';
import MainLocal from './MainLocal';

import { getShelfs } from '../../../../server/DataShelf';

//import { LocalsContext } from '../../../../dashboard';


export default function ConfigLocales(props) {
    const [infoOpen, setInfoOpen] = React.useState(false);
    const [localSelected, setLocal] = React.useState();
    const [arrLocal, setArrLocal] = React.useState();

    //const arrayLocal = React.useContext(LocalsContext);
    const posLocales = {
        1:
        {   minA:-150,
            maxA:-30,
            target:"-4.6 1.6 21.6"
        },
        2:
        {   minA:-179,
            maxA:-90,
            target:"-7.83 1.6 32.27"
        },
        3:
        {   minA:-179,
            maxA:-90,
            target:"-13.8 1.6 37.014"
        },
        4:
        {   minA:-175,
            maxA:-105,
            target:"-18.745 1.6 43.2"
        },
        5:
        {   minA:90,
            maxA:179,
            target:"-25.55 1.6 32.545"
        },
        6:
        {   minA:90,
            maxA:179,
            target:"-33.0 1.6 23.754"
        },
        7:
        {   minA:90,
            maxA:179,
            target:"-40.34 1.6 14.85"
        },
        8:
        {   minA:90,
            maxA:179,
            target:"-47.91 1.6 6.21"
        },
        9:{   minA: 1,
            maxA: 90,
            target:"-51.907 1.6 -5.357"
        },
        10:{   minA: 1,
            maxA: 90,
            target:"-37.819 1.6 -16.86"
        },
        11:{   minA: 1,
            maxA: 90,
            target:"-23.86 1.6 -28.66"
        },
        12:{   minA: 1,
            maxA: 90,
            target:"-10.02 1.6 -40.33"
        },
        13:{   minA: 1,
            maxA: 90,
            target:"3.78 1.6 -51.81"
        },
        14:{   minA: -90,
            maxA: -1,
            target:"14.57 1.6 -45.95"
        },
        15:{   minA: -90,
            maxA: -1,
            target:"21.78 1.6 -37.10"
        },
        16:{   minA: -90,
            maxA: -1,
            target:"29.197 1.6 -28.24"
        },
        17:{   minA: -90,
            maxA: -1,
            target:"36.78 1.6 -19.71"
        },
        18:{   minA: -165,
            maxA: -90,
            target:"45.71 1.6 -11.12"
        },
        19:{   minA: -179,
            maxA: -90,
            target:"39.74 1.6 -6.174"
        },
        20:{   minA: -179,
            maxA: -90,
            target:"33.85 1.6 -1.20"
        },
        21:{   minA: 135,
            maxA: 179,
            target:"22.21 1.6 -0.636"
        },
    }

    function openLocal(clave,obj) {
        setInfoOpen(true)

        //document.getElementById("player-camera").setAttribute('camera', 'active', false)
        document.getElementById("locales-camera").setAttribute('camera', 'active', true)
        getShelfs(obj)
        setLocal(clave)
        setArrLocal(obj)
        activeLocalModel(obj.tipo_distribucion_local)
        props.setOpen(true)

        let el = posLocales[clave]
        document.getElementById("locales-camera").setAttribute('orbit-controls', {minAzimuthAngle:el.minA,maxAzimuthAngle:el.maxA,target:el.target})

        
    }

    function activeLocalModel(i){
        document.getElementById("local-tipo1").setAttribute("visible",false)
        document.getElementById("local-tipo2").setAttribute("visible",false)
        document.getElementById("local-tipo3").setAttribute("visible",false)

        document.getElementById("local-tipo"+i).setAttribute("visible",true)
    }

    function backLocal() {
        setInfoOpen(false)
        props.setOpen(false)

        document.getElementById("player-camera").setAttribute("camera",{active:true})
        //document.getElementById("locales-camera").setAttribute("camera",{active:false})

    }

    function loadLocalCard(){
        const arr = JSON.parse(localStorage.getItem('localsArray'));
        let rows = [];
        let key
        let id

        arr.sort((a, b) => a.clave_local - b.clave_local);

        arr.forEach(el => {
            key = el.clave_local
            id = el.id_local

            rows.push(
                <CustomCard arrobject={el} size="md" key={'k'+key} number={key} type={2} image="Local2_1.jpg" handleClick={openLocal}></CustomCard>
            )

            
        });

        

        return <Box sx={{ display: "flex", alignItems: "flex-end", padding: "0.3rem" }}>{rows}</Box>


    }


    return (
        <Box sx={{ width: "97%", height: "100%", display: "flex" }}>
            <Box sx={{ width: '15%', height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                <Typography sx={{ textAlign: 'center', color: 'white' }} variant='h6'>Configuración locales</Typography>
            </Box>
            <Divider orientation="vertical" variant="middle" flexItem />

            {/* Dividir en componentes */}
            <Box sx={{ width: '85%', height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>

                {!infoOpen &&
                    <Box sx={{ display: "flex", alignItems: "center", padding: "0.2rem 1rem", width: "95%", justifyContent: "flex-start", overflowX:"auto" }}>

                        {/* <CustomCard size="md" number="1" type={2} image="Local2_1.jpg" handleClick={openLocal}></CustomCard> */}
                        {loadLocalCard()}
                        
                    </Box>

                    
                }
                {infoOpen &&
                    <MainLocal handleClickBack={backLocal} number={localSelected} arr={arrLocal}></MainLocal>
                }


            </Box>
        </Box>
    )
}